import React, { useState, useEffect } from "react";

import { FaTimes, FaAngleLeft, FaAngleRight } from "react-icons/fa";
import Button from "../Button";

import styles from "./style.module.css";
import Loader from "../Loader";

type Props = {
  images: Array<string>;
  handleClose: () => void;
};

const Gallery: React.FC<Props> = ({ images, handleClose }) => {
  const [indexToShow, setIndexToShow] = useState(0);
  const [imageLoaded, setImageLoaded] = useState(false);
  useEffect(() => {
    setImageLoaded(false);
  }, [indexToShow]);
  useEffect(() => {
    document.body.style.overflowY = "hidden";
    return () => {
      document.body.style.overflowY = "scroll";
    };
  }, []);
  return (
    <div className={styles.gallery}>
      <div className={styles.closeButton}>
        <Button
          onClick={() => handleClose()}
          color="light"
          variant="transparent"
          size="l"
          icon
        >
          <FaTimes></FaTimes>
        </Button>
      </div>
      <div className={styles.prevButton}>
        {indexToShow > 0 && (
          <Button
            onClick={() => setIndexToShow(indexToShow - 1)}
            color="light"
            variant="transparent"
            size="l"
            icon
          >
            <FaAngleLeft></FaAngleLeft>
          </Button>
        )}
      </div>
      <a href={images[indexToShow]} className={styles.imageContainer}>
        <img
          src={images[indexToShow]}
          alt={`Preview of ${indexToShow + 1}`}
          onLoad={() => setImageLoaded(true)}
          className={imageLoaded ? styles.loadedImage : styles.unloadedImage}
        ></img>
        {!imageLoaded && <Loader></Loader>}
      </a>
      <div className={styles.nextButton}>
        {indexToShow < images.length - 1 && (
          <Button
            onClick={() => setIndexToShow(indexToShow + 1)}
            color="light"
            variant="transparent"
            size="l"
            icon
          >
            <FaAngleRight></FaAngleRight>
          </Button>
        )}
      </div>
    </div>
  );
};

export default Gallery;
